import './detectingJS';
import './menuToggle';

const video = document.querySelector('.video');
const playPauseBtn = document.querySelector('.play-btn');
const fullScreenBtn = document.querySelector('.fullscreen-btn');
const videoPlayerWrp = document.querySelector('.media-player-wrp');
const controls = document.querySelector('.controls-wrp');
const playBtnSvg = document.querySelector('.play-svg');
const pauseBtnSvg = document.querySelector('.pause-svg');
const volumeOffSvg = document.querySelector('.volume-off-svg');
const volumeOnSvg = document.querySelector('.volume-on-svg');
const muteBtn = document.querySelector('.mute-btn');
const progressIndicatorBtn = document.querySelector('.progress-indicator-btn');
const sliderElem = document.querySelector('.progress-indicator-wrp');
const thumbElem = document.querySelector('.progress-indicator-btn');

// Скрыть стандартное управление
video.controls = false;

const changeBtnIcon = (action, btn) => {
  action === 'hide'
    ? btn.style.display = 'none'
    : btn.style.display = 'block';
};

// Воспроизведени / Пауза по клику
const playPauseBtnClickHandler = (evt) => {
  evt.preventDefault();

  // Если видео на паузе или проигранно до конца
  if (video.paused || video.ended) {
    video.play();
    changeBtnIcon('hide', playBtnSvg);
    changeBtnIcon('show', pauseBtnSvg);
  } else {
    video.pause();
    changeBtnIcon('show', playBtnSvg);
    changeBtnIcon('hide', pauseBtnSvg);
  }
};

// Воспроизведени / Пауза по клику кнопки
playPauseBtn.addEventListener('click', playPauseBtnClickHandler);

// Воспроизведени / Пауза по клику картинки видео
video.addEventListener('click', playPauseBtnClickHandler);

// Выключить / включить звук
const muteBtnClickHandler = (evt) => {
  evt.preventDefault();

  if (video.muted) {
    video.muted = false;
    changeBtnIcon('show', volumeOnSvg);
    changeBtnIcon('hide', volumeOffSvg);
  } else {
    video.muted = true;
    changeBtnIcon('hide', volumeOnSvg);
    changeBtnIcon('show', volumeOffSvg);
  }
};

muteBtn.addEventListener('click', muteBtnClickHandler);

// Поддержка полноэкранного режима
const fullScreenEnabled = !!(document.fullscreenEnabled || document.mozFullScreenEnabled
|| document.msFullscreenEnabled
|| document.webkitSupportsFullscreen
|| document.webkitFullscreenEnabled
|| document.createElement('video').webkitRequestFullScreen);

// Скрыть кнопку если не поддерживается полноэкранный режим
if (!fullScreenEnabled) {
  fullScreenBtn.style.display = 'none';
}

// Проверка включен ли полноэкранный режим
const isFullScreen = () => !!(document.fullScreen
|| document.webkitIsFullScreen
|| document.mozFullScreen
|| document.msFullscreenElement
|| document.fullscreenElement);

// Включение / выключение полноэкранного режима
const fullscreenClickHandler = (evt) => {
  evt.preventDefault();

  // Изменение стилей в полноэкранном режиме видео
  video.classList.add('video-fullscreen-on');
  controls.classList.add('controls-fullscreen-on');

  if (isFullScreen()) {
    // Отключить стили для полноэкранного режима
    video.classList.remove('video-fullscreen-on');
    controls.classList.remove('controls-fullscreen-on');

    if (document.exitFullscreen) {
      document.exitFullscreen();
    } else if (document.mozCancelFullScreen) {
      document.mozCancelFullScreen();
    } else if (document.webkitCancelFullScreen) {
      document.webkitCancelFullScreen();
    } else if (document.msExitFullscreen) {
      document.msExitFullscreen();
    }
  } else if (videoPlayerWrp.requestFullscreen) {
    videoPlayerWrp.requestFullscreen();
  } else if (videoPlayerWrp.mozRequestFullScreen) {
    videoPlayerWrp.mozRequestFullScreen();
  } else if (videoPlayerWrp.webkitRequestFullScreen) {
    videoPlayerWrp.webkitRequestFullScreen();
  } else if (videoPlayerWrp.msRequestFullscreen) {
    videoPlayerWrp.msRequestFullscreen();
  }
};

fullScreenBtn.addEventListener('click', fullscreenClickHandler);


// Прогресс индикатор видео следует за его воспроизведением
const videoTimeUpdateHandler = () => {
  const currentVideoTime = Math.floor(video.currentTime / video.duration * 100);

  progressIndicatorBtn.style.left = `${currentVideoTime}%`;

  if (video.ended) {
    // Передвинуть индикатор прогресса видео на начало
    progressIndicatorBtn.style.left = '0px';
    changeBtnIcon('show', playBtnSvg);
    changeBtnIcon('hide', pauseBtnSvg);
  }
};

video.addEventListener('timeupdate', videoTimeUpdateHandler);

// Изменить текущую позицию видео при клике на полосе индикатора воспроизведения
sliderElem.addEventListener('click', (evt) => {
  video.currentTime = `${video.duration
    * (evt.offsetX / evt.target.clientWidth)}`;
});

// Скрытие / отображение контролов при наведении мыши на видео
const controlsclickHandler = () => {
  controls.classList.add('hide-menu-active');

  controls.removeEventListener('click', controlsclickHandler);
};

controls.addEventListener('click', controlsclickHandler);

// Перетаскивание индикатора плеера

const getCoords = (elem) => { // кроме IE8-
  const box = elem.getBoundingClientRect();

  return {
    top: box.top + window.pageYOffset,
    left: box.left + window.pageXOffset,
  };
};

const thumbElemMouseDownHandler = (evt) => {
  const thumbCoords = getCoords(thumbElem);
  const shiftX = evt.pageX - thumbCoords.left;

  const sliderCoords = getCoords(sliderElem);

  const documentMouseMoveHandler = (moveEvt) => {
    let newLeft = moveEvt.pageX - shiftX - sliderCoords.left;

    // курсор ушёл вне слайдера
    if (newLeft < 0) {
      newLeft = 0;
    }

    const rightEdge = sliderElem.offsetWidth - thumbElem.offsetWidth;
    if (newLeft > rightEdge) {
      newLeft = rightEdge;
    }

    thumbElem.style.left = `${newLeft}px`;

    video.currentTime = video.duration * (newLeft / rightEdge);
  };

  const documentMouseUpHandler = (upEvt) => {
    upEvt.preventDefault();

    document.removeEventListener('mousemove', documentMouseMoveHandler);
    document.removeEventListener('mouseup', documentMouseUpHandler);
  };

  document.addEventListener('mousemove', documentMouseMoveHandler);
  document.addEventListener('mouseup', documentMouseUpHandler);

  return false; // disable selection start (cursor change)
};

thumbElem.addEventListener('mousedown', thumbElemMouseDownHandler);
